<template>
    <li class="saved-move">
        <span>{{ index + 1 }}.</span>
        <div :class="piece"></div>
        <span>{{ move.san }}</span>
        <span class="text-smaller">({{ formatTime(move.duration) }})</span>
        <span v-if="move.event !== ''">{{move.event}}!</span>
    </li>
</template>

<script>
export default {
    props: {
        index: Number,
        move: Object
    },
    computed: {
        piece() {
            return `fen-${this.move.piece}-${this.move.color} icon`;
        }
    },
    methods: {
        formatTime(sec) {
            const minutes = Math.floor(((sec / 1000) % 3600) / 60);
            const seconds = Math.floor(((sec / 1000) % 3600) % 60);
            return `${('0' + minutes).slice(-2)}:${('0' + seconds).slice(-2)}`;
        }
    }
}
</script>

<style scoped>
.saved-move {
    display: flex;
    align-items: flex-end;
}

.saved-move > * {
    margin-right: 3px;
}

.icon {
    height: 1rem;
    width: 1rem;
    background-position: center;
    background-size: contain;
}

.fen-b-w{
    background-image: url("../assets/bishop_white.png");
}
.fen-b-b{
    background-image: url("../assets/bishop_black.png");
}
