<template>
    <div class="frame" :class="rotate">
        <div class="board">
            <template v-for="row in 8" :key="row">
                <div class="row">
                    <template v-for="column in 8" :key="column">
                        <BoardSquare 
                            :row="row"
                            :column="column"
                            :highlights="highlights"
                            @square-selected="squareSelected">
                        </BoardSquare>
                    </template> 
                </div>
            </template> 
            <template v-for="piece in pieces" :key="piece.id">
                <BoardPiece 
                    :piece="piece"
                    @piece-selected="pieceSelected">
                </BoardPiece>
            </template>
        </div>
        <BoardLabel class="label-top" :orientation="'horizontal'"/>
        <BoardLabel class="label-left" :orientation="'vertical'"/>
        <BoardLabel class="label-bottom" :orientation="'horizontal'"/>
        <BoardLabel class="label-right" :orientation="'vertical'"/>
        <button class="btn-flip" @click="flipBoard">F</button>
    </div>
</template>

<script>
import BoardSquare from "./BoardSquare.vue"
import BoardPiece from "./BoardPiece.vue"
import BoardLabel from "./BoardLabel.vue"

export default {
    components: {
        BoardSquare,
        BoardLabel,
        BoardPiece
    },
    props: {
        pieces: Array,
        legal: Array,
        turn: String
    },
    data() {
        return {
            source: "",
            moveStarted: false,
            flip: false
        }
    },
    methods: {
        squareSelected(square) {
            if((this.legalTargets()).includes(square) && this.moveStarted) {
                this.move(square);
                this.moveStarted = false;
            } else {
                this.moveStarted = false;
            }
        },
        pieceSelected(piece) {
            if(piece.color === this.turn.color) {
                this.moveStarted = true;
                this.source = piece.position;
            } else if(piece.color !== this.turn.color && this.moveStarted) {
                this.move(piece.position);
                this.moveStarted = false;
            }
        },
        move(target) {
            this.$emit("make-move", { from: this.source, to: target });
        },
        legalTargets() {
            const targets = this.legal.filter(move => move.from === this.source);
            return targets.map(element => element.to);
        },
        flipBoard() {
            this.flip = !this.flip;
        }
    },
    computed: {
        highlights() {
            if (this.moveStarted) {
                return [...this.legalTargets(), this.source];
            } else {
                return [];
            }
        },
        rotate() {
            return `flip-${this.flip}`;
        }
    }
}
</script>

<style scoped>
.frame {
    width: 100%;
    border: 1px solid black;
    background-color: white;
    aspect-ratio: 1 / 1;
    display: grid;
    grid-template-columns: 1.2rem auto 1.2rem;
    grid-template-rows: 1.2rem auto 1.2rem;
    grid-template-areas: ". top ." "left center right" ". bottom flip";
    transition: transform 1s;
}
.board {
    position: relative;
    grid-area: center;
    border: 1px solid black;
    width: 100%;
    height: 100%;
}
.row {
    display: flex;
    width: 100%;
    height: 12.5%;
}
.label-top {
    grid-area: top;
}
.label-left {
    grid-area: left;
}
.label-bottom {
    grid-area: bottom;
}
.label-right {
    grid-area: right;
}
.btn-flip {
    grid-area: flip;
}
.flip-true {
    transform: rotate(0.5turn);
}
.flip-true :deep(.piece) {
    transform: rotate(0.5turn);
}
.flip-true :deep(.label) {
    transform: rotate(0.5turn);
}
</style>