<template>
  <div>
    <PageHeader
        @start-game="start"
        @join-game="join"/>
    <div class="container content">
      <div class="action">
        <div class="game">
          <ChessBoard 
            :pieces="game.pieces" 
            :legal="game.legal" 
            :turn="game.turn"
            @make-move="move"/>
        </div>
        <StatusBox
          :game="game"
          :id="id"/>
      </div>
      <div>
        <StatisticsBox
          :game="game"
          :id="id"/>
      </div>
    </div>
  </div>
</template>

<script>
import ChessBoard from './components/Board.vue'
import PageHeader from "./components/Header.vue"
import StatusBox from "./components/Status.vue"
import StatisticsBox from "./components/Statistics.vue"

import { io } from "socket.io-client"

export default {
  name: 'App',
  components: {
    ChessBoard,
    PageHeader,
    StatusBox,
    StatisticsBox
  },
  data() {
    return {
      game: {},
      socket: io(),
      id: ""
    }
  },
  methods: {
    start(fen) {
        this.socket.emit("game:start", fen);
    },
    join(id) {
        this.socket.emit("game:join", id);
        this.id = id;
        sessionStorage.setItem('id', this.id);
    },
    move(move) {
      this.socket.emit("game:move", {id: this.id, move: move});
    }
  },
  mounted() {
    this.socket.on("game:started", (args) => {
      this.game = args;
      this.id = args.whitePlayer.id;
      sessionStorage.setItem('id', this.id);
    });
    this.socket.on('game:joined', args => {
      this.game = args;
    });
    this.socket.on('game:update', args => {
      this.game = args;
    });
  }
}
</script>

