<template>
    <div class="status">
        <div>
            <h5>Game clock</h5>
            <p><status-clock 
                :referenceTime="game.timeStart"
                :pulse="pulse"/></p>
        </div>
        <div>
            <h5>Move duration</h5>
            <p><status-clock 
                :referenceTime="game.timeLastMove"
                :pulse="pulse"/></p>
        </div>
        <div>
            <h5>Current turn</h5>
            <p>{{ turn }}</p>
        </div>
        <div>
            <h5>White Player<span v-if="id === white"> (you)</span></h5>
            <div class="copy-line">
                <p>{{ white }}</p>
                <button @click="copy(white)">Copy</button>
            </div>
        </div>
        <div>
            <h5>Black Player<span v-if="id === black"> (you)</span></h5>
            <div class="copy-line">
                <p>{{ black }}</p>
                <button @click="copy(black)">Copy</button>
            </div>
        </div>
        <div>
            <h5>FEN</h5>
            <div class="copy-line">
                <p>{{ fen }}</p>
                <button @click="copy(fen)">Copy</button>
            </div>
        </div>
    </div>
</template>

<script>
import StatusClock from "./StatusClock.vue"

export default {
    components: {
        StatusClock
    },
    props: {
        game: Object,
        id: String
    },
    data() {
        return {
            pulse: true
        }
    },
    computed: {
        turn() {
            return this.game.turn ? (this.game.turn.id === this.game.whitePlayer.id) ? "white" : "black" : "-";
        },
        white() {
            return this.game.whitePlayer ? this.game.whitePlayer.id : "-";
        },
        black() {
            return this.game.blackPlayer ? this.game.blackPlayer.id : "-";
        },
        fen() {
            return this.game.fen ? this.game.fen : "-";
        }
    },
    methods: {
        copy(string) {
            navigator.clipboard.writeText(string);
        }
    },
    mounted() {
        setInterval(() => {
            this.pulse = !this.pulse;
        }, 1000);
    }
}
</script>

<style scoped>
.status {
    background-color: #B4B8AB;
    padding: 5px 5px 0 5px;
    margin-bottom: 5px;
    overflow: auto;
}

.status > div {
    margin-bottom: 5px;
}

.status > div > h5 {
    color: #111;
    font-weight: normal;
}

@media (min-width: 970px) {
    .status {
        height: 480px;
        width: 480px;
        margin-left: 5px;
    }
}

.copy-line {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}
</style>