<template>
    <div 
        class="piece" 
        :class="[fen, row, column]"
        @click="click">
    </div>
</template>

<script>
export default {
    props: {
        piece: Object
    },
    computed: {
        fen() {
            return `fen-${this.piece.type}-${this.piece.color}`;
        },
        row() {
            return `row-${this.piece.position.slice(-1)}`;
        },
        column() {
            return `col-${this.piece.position.slice(0,1)}`;
        }
    },
    methods: {
        click() {
            this.$emit("piece-selected", this.piece);
        }
    }
}
</script>

