<template>
    <div 
        class="square"
        :class="[color, highlight]"
        @click="click">
    </div>
</template>

<script>
export default {
    props: {
        row: Number,
        column: Number,
        board: Array,
        highlights: Array
    },
    computed: {
        color() {
            if ((this.row % 2 !== 0) ? (this.column % 2 !== 0) : (this.column % 2 === 0)) {
                return "light";
            } else {
                return "dark";
            }
        },
        highlight() {
            if (this.highlights.includes(this.id)) {
                return "highlight";
            } else {
                return "";
            }
        },
        id() {
            return `${String.fromCharCode(96 + this.column)}${9 - this.row}`;
        } 
    },
    methods: {
        click() {
            this.$emit("square-selected", this.id);
        }
    }
}
</script>

<style scoped>
.square {
    width: 12.5%;
    height: 100%;
}

.light {
    background-color: #ffce9e;
}

.dark {
    background-color: #d18b47;
}

.light.highlight {
    background-color: #00FF00;
}

.dark.highlight {
    background-color: #00E500;
}
</style>