<template>
    <div class="label-group" :class="group">
        <template v-for="i in 8" :key="i">
            <div :class="label">
                <div class="label">{{ char(i) }}</div>
            </div>
        </template>
    </div>
</template>

<script>
export default {
    props: {
        orientation: String
    },
    computed: {
        group() {
            return (this.orientation === "horizontal") ? "label-group-horizontal" : "label-group-vertical";
        },
        label() {
            return (this.orientation === "horizontal") ? "label-horizontal" : "label-vertical";
        }
    },
    methods: {
        char(i) {
            return (this.orientation === "horizontal") ? String.fromCharCode(96 + i) : (9 - i);
        }
    }
}
</script>

<style>
.label {
    transition: transform 1.2s;
}

.label-group {
    width: 100%;
    height: 100%;
    text-align: center;
}

.label-group-horizontal {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.label-group-vertical {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.label-horizontal {
    width: 12.5%;
}

.label-vertical {
    display: flex;
    height: 12.5%;
    align-items: center;
    justify-items: center;
}
</style>