<template>
    <div class="header">
        <div class="container">
            <button @click="start">Start</button>
            <button @click="join">Join</button>
            <input v-model="input" placeholder="Game ID or FEN string" />
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            input: ""
        }
    },
    methods: {
        start() {
            this.$emit("start-game", this.input);
            this.input = "";
        },
        join() {
            this.$emit("join-game", this.input);
            this.input = "";
        }
    }
}
</script>

<style scoped>
.header {
    background-color: #153243;
    padding: 5px 0;
}
button {
    min-width: 45px;
}
</style>